	module.exports = {"data":{"markdownRemark":{"html":"<p>まぁ散々な言われようですが、良くも悪くも<a href=\"/!/thA/\">時封城</a>がかなり広まってくれたようで何よりですね。</p>\n<p>さて早速ですが次作の話でも。ちょいと事情で次作と次々作は同時進行で制作始めています。極論次々作が先に出てしまうこともあり得ます。と言うのも次作がちょいと初の試みなので正直どれだけの制作期間を要するか予想がつかないもので……(時封城はぶっちゃけ従来までのステージ3作で色々培った部分があったので……) ただ、それぞれストーリーの繋がりがほとんどないのでその辺はご安心いただければと思います。それこそ 紅魔郷～永夜抄に対し萃夢想みたいな感じを連想して貰えれば丁度そんな感じですので次作は若干の肉弾戦要素を含んでいるところも似てますな。 ネタバレするには過ぎるくらい絶望的に完成度がないので続きはまた今度にでも。</p>","frontmatter":{"cover":null,"date":"2005-08-26T01:54:00+09:00","strDate":"2005/8/25","title":"次作作りますぜ"}}},"pathContext":{"path":"/ja/blog/2005/08/26-次作作りますぜ/","langKey":"ja"}}

